import Vue from 'vue';
import Router from 'vue-router';
import Home from '@/containers/home';
import Marquee from '@/containers/marquee';

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home,
    },
  ],
});



