<script>
  import Marquee from '@/containers/marquee'; // eslint-disable-line

  export default {
    name: 'home',
    data() {
      return {
        guessError: 0,
        guess: undefined,
        correct: '-1',
        showMarquee: false,
      };
    },
    components: {
      'my-marquee': Marquee,
    },
    created() {
      this.correct = String(Math.floor((new Date() - new Date(2017, 7, 25 - 1)) / 86400000));
    },
    methods: {
      onGuess() {
        if (this.guess === undefined || this.guess === '') {
          this.guessError = -2;
          return;
        }
        if (this.guess === this.correct) {
          this.guessError = 0;
          this.showMarquee = true;
        } else if (Number(this.guess) < Number(this.correct)) {
          this.guessError = -1;
        } else {
          this.guessError = 1;
        }
      },
    },
  };
</script>

<template src="./template.html"></template>

<style src="./style.sass" lang="sass" scoped></style>



